body {
    margin: 0;
    background-image: url('./background.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

html {
    font-family: 'Poppins', sans-serif;
}
/* #root {
    background-image: url('./undraw_adventure_4hum.svg');
    background-repeat: no-repeat;
    background-position: right -45px;
} */

.wrapper {
    background-image: url('./undraw_adventure_4hum.svg');
    background-repeat: no-repeat;
    background-position-x: right;
    max-width: 400px;
    margin: auto;
    background-color: white;
    padding: 2rem;
    padding-block-end: 4rem;
    padding-block-start: 10rem;
    margin-block-end: 2rem;
    border-radius: 24px;
}

h1 {
    text-align: center;
    color: white;
}

h3 {
    /* background-image: url('./undraw_adventure_4hum.svg');
    background-repeat: no-repeat;
    background-position: right -45px; */
    font-size: 24px;
    line-height: 36px;
    color: #2F527B;
}

button {
    font-size: 18px;
    line-height: 27px;
    padding-block-start: 15px;
    padding-block-end: 16px;
    width: 100%;
    text-align: start;
    border-radius: 12px;
    margin-block-end: 25px;
    background-color: white;
    color: rgba(96, 102, 208, 0.8);
    border: 2px solid rgba(96, 102, 208, 0.7);
}

/* button:hover {
    background-color: #F9A826;
    color: white;
} */

.win {
    background-color: green;
}

.loose {
    background-color: red;
}

.wrongAnswer {
    background-color: #FF8A65;
}

span {
    font-size: 18px;
    line-height: 27px;
    padding-inline-end: 46px;
}

.score {
    color: yellowgreen;
    font-size: 24px;
    padding-inline-end: 0;
}

.btnWrapper {
    position: relative;
}

.nextBtn {
    /* position: absolute;
    right: 0; */
    font-weight: bold;
    font-size: 18px;
    line-height: 27px;
    color: #FFFFFF;
    padding-block-start: 15px;
    padding-block-end: 14px;
    padding-inline-start: 37px;
    padding-inline-end: 37px;
    margin-block-start: -7px;
    width: auto;
    background-color: #F9A826;
    box-shadow: 0px 2px 4px rgba(252, 168, 47, 0.4);
    border-radius: 12px;
    border: none;
}

.result {
    background-image: url('./undraw_winners_ao2o.svg');
    background-repeat: no-repeat;
    background-position: top;
    max-width: 400px;
    margin: auto;
    background-color: white;
    padding: 2rem;
    padding-block-end: 4rem;
    padding-block-start: 10rem;
    margin-block-end: 2rem;
    border-radius: 24px; 
    text-align: center;
}

.resultBtn {
    font-size: 18px;
    line-height: 27px;
    padding-block-start: 15px;
    padding-block-end: 16px;
    width: auto;
    text-align: start;
    border-radius: 12px;
    margin-block-end: 25px;
    background-color: white;
    color: rgba(96, 102, 208, 0.8);
    border: 2px solid rgba(96, 102, 208, 0.7);
    padding-inline-end: 31px;
    padding-inline-start: 31px;
}

a {
    text-decoration: none;
}

img {
    max-width: 50%;
}
